.services{
  display: inline-block;
  background-color: indianred;
  width: calc(75% / 3);
  height: 300px;
  border: 1px solid dimgrey;

 .services-text {
    h1{
      font-size: 30px;
      margin: 30px 0 ;
    }
  }
}



.services img{
  padding: 20px 20px 40px 20px ;
}
.services-text{
  padding: 0px 20px ;
  width: 550px;
}
.services .services-text h1{
  font-size: 20px;
  margin-bottom: 10px;
}
.services .services-text p{
  text-align: center;
}

.sb1{
  background: rgb(218,177,72);
  background: linear-gradient(4deg, rgba(218,177,72,1) 0%, rgb(234, 204, 123) 100%);
}

