  background: rgb(72, 196, 218);
  background: linear-gradient(4deg, rgb(72, 196, 218) 0%, rgb(126, 237, 241) 100%);
}

.sb3{
  background: rgb(140, 72, 218);
  background: linear-gradient(4deg, rgb(140, 72, 218) 0%, rgb(131, 94, 222) 100%);
}

.sb4{
  background: rgb(92, 87, 234);
  background: linear-gradient(4deg, rgb(92, 87, 234) 0%, rgb(112, 105, 250) 100%);
}

.sb5{
  background: rgb(186, 122, 239);
